@import '../../global.scss';

.contactContainer {
    position: relative;
    width: 100%;
    overflow-x: hidden;

    header {
        height: 100vh;

        @media (max-height: 850px) {
            height: 150vh;
        }

        background: url('../../assets/Contact/background.jpg');
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        display: flex;
        justify-content: center;
        align-items: center;
        color: white;
        flex-direction: column;
        padding: 0;
        box-sizing: border-box;

        @media (min-width: $mobile) {
            padding: 0 $page-padding-x-m;
        }

        @media (min-width: $tablet-small) {
            padding: 0 $page-padding-x-ts;
        }

        @media (min-width: $tablet-large) {
            padding: 0 $page-padding-x-tl;
        }

        @media (min-width: $desktop-small) {
            padding: 0 $page-padding-x-ds;
        }

        @media (min-width: $desktop-large) {
            padding: 0 $page-padding-x-dl;
        }

        h1 {
            font-style: normal;
            font-weight: 500;
            font-size: 80px;
            line-height: 140%;
            text-align: center;
            width: 75%;
            margin: 0 auto 2rem;
            max-width: 1200px;

            @media (min-width: $mobile) {
                font-size: 44px;
                width: 100%;
            }

            @media (min-width: $tablet-small) {
                font-size: 60px;
                width: 75%;
            }

            @media (min-width: $tablet-large) {
                font-size: 70px;
                width: 75%;
            }

            @media (min-width: $desktop-small) {
                font-size: 80px;
                width: 75%;
            }

            @media (min-width: $desktop-large) {
                font-size: 80px;
                width: 75%;
            }
        }
    }
}